* {
  box-sizing: border-box;
}

body {
  background-color: #2f3945;
}

#calculator {
  width: 470px;
  margin: auto;
  border: 1px solid rgb(160, 159, 159);
  padding: 10px;
  border-radius: 5px;
  background-color: #ebebeb;
  box-shadow: black 5px 5px;
}

#button-container {
  display: grid;
  height: 300px;
  grid-template-areas:
    "adv-calc-keys    basic-calc-keys"
    "history          basic-calc-keys";
  grid-template-rows: 60% 40%;
  grid-template-columns: 1fr 1fr;
}

#basic-calc-keys {
  grid-area: basic-calc-keys;
  display: grid;
  grid-template-areas:
    "l-bracket   r-bracket   clear     clear-ele"
    "seven       eight       nine      divide"
    "four        five        six       multiply"
    "one         two         three     subtract"
    "zero        decimal     equals    add";
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(4, 1fr);
}

#adv-calc-keys {
  grid-area: adv-calc-keys;
  display: grid;
  grid-template-areas:
    "ans        mod      factorial   sqrt"
    "pi          log         ln        exponent"
    "e           sin         cos       tan";
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(4, 1fr);
}

.calc-button {
  margin: 2px;
  border-radius: 4px;
  background: #E5E9F0;
  font-weight: 600;
  background: linear-gradient(white 0%, #e6e6e6 100%);
}

#adv-calc-keys .calc-button, #l-bracket, #r-bracket, #clear, #clear-ele, #divide, #multiply, #subtract, #add
{
  background: linear-gradient(rgb(240, 240, 240) 0%, rgb(179, 183, 190) 100%);
}

#equals {
  background: linear-gradient(#a0c2f6 0%, #74a7fa 100%);
}

#deg-rad-area {
  grid-area: deg-rad-area;
  display: flex;
  justify-content: center;
}

#history-button-area {
  grid-area: history;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}

#history-button {
  width: 90%;
  margin-bottom: 3px;
  border-radius: 5px;
  font-size: 1.3rem;
  border: grey solid 1px;
  background: linear-gradient(white 0%, #e6e6e6 100%);
}

#history {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  width: 500px;
  height: 265px;
  border: 1px solid black;
  border-radius: 5px;
  position: absolute;
  top: -185px;
  left: -26px;
  background-color: white;
  overflow-y: auto;
  box-shadow: 2px 2px rgba(0,0,0,0.5);
  padding: 4px;
}

.history-item p {
  margin: 3px 0;;
}

.history-item {
  display: flex;
  justify-content: space-between;
  margin: 0;
}

.history-item p {
  color: black;
}

.history-item p:hover {
  color: blue;
  cursor: pointer;
}

@media(max-width: 470px) {
  #calculator {
    width: 320px;
  }

  #button-container {
    height: 600px;
    grid-template-areas:
      "basic-calc-keys"
      "."
      "adv-calc-keys"
      "history";
      grid-template-rows: 50% 10% 30% 10%;
      grid-template-columns: 1fr;
  }

  #history {
    width: 320px;
    top: -250px;
    left: -11px;
  }
}

#display {
  display: flex;
  flex-direction: column;
  height: 80px;
  border: 1px solid rgb(160, 159, 159);
  overflow: hidden;
  white-space: nowrap;
  margin-bottom: 10px;
  border-radius: 5px;
  padding-right: 5px;
  background-color: white;
}

#top-display {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 30%;
}

#bottom-display {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 70%;
  font-size: 2rem;
}

